      <div class="desktop">

        <div class="adk_grid_toolbar" style="    grid-template-columns: auto auto 1fr;">

            <div style="display: grid;place-self: center start;padding-right: 10px;">
                <div class="uk-button-group">
                    <div class="uk-inline">

                        <button v-on:click="goBack()"  class="uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(226 226 226);border-radius: 3px;place-self: center start;place-items: center;font-size: 0.65rem;line-height: 32px;font-weight: normal !important;padding: 0px 7px;"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="18" height="18"><path fill="currentColor" d="M12 4l1.4 1.4L7.8 11H20v2H7.8l5.6 5.6L12 20l-8-8 8-8z" style="fill: #5a5a5a;"></path></svg></button>

                        <!--
                        <button v-on:click="goBack()" class="uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(240, 80, 110);border-radius: 3px;place-self: center start;place-items: center;min-width: 100px;font-size: 0.65rem;line-height: 32px;font-weight: normal !important;"><svg width="20" height="20" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg" data-svg="triangle-left" style="fill: white;margin-top: -3px;"><polygon points="12 5 7 10 12 15"></polygon></svg><span style=""> Back</span></button>
