            <div style="display: grid;place-self: center start;text-align: left;">
                <div style="display: grid;place-self: center start;padding-left: 0px;padding-right: 20px;color: rgba(61, 61, 61, 0.78) !important;grid-template-columns: auto auto;font-size: 0.75rem;">
                   <span>
                    Super Admin Page <span uk-icon="icon:triangle-right;ratio:1"></span> {{tenantName}} <span uk-icon="icon:triangle-right;ratio:1"></span> Users ({{list.length}})
                   </span>
                </div>
            </div>
            <div style="display: grid;grid-gap: 10px;grid-template-columns: auto auto;place-self: center end;text-align: right;">
                <a  v-bind:href="'#/tenant/users/new' + '/' + tenantId + '/' + tenantName" class=" uk-button uk-button-small uk-button-default ui_button_small_green ui_button_small"
                style="cursor: pointer;background: rgb(144, 194, 62);color: white !important;line-height: 25px;min-width: 125px;border: 0.05em solid rgb(131, 181, 50);line-height: 32px !important;border-radius: 3px;">
                      <span class="" style="margin-right: 5px!important;" uk-icon="icon:plus;ratio:0.45"></span>New User
                </a>
                <div class="uk-inline">
                      <div class="uk-inline">
                           <span class="uk-form-icon" uk-icon="icon: search;ratio:0.65"></span>
                           <input class="uk-input uk-form-width-medium uk-form-small" style="border: 0.1em solid rgb(173, 173, 173);border-radius: 2px;height: 35px;width: 100%;" type="text"
                           v-model="searchQuery"
                           placeholder="Search.."
                           v-on:keyup.enter.stop="getSearchList()"
                           >
                      </div>
                </div>
            </div>

        </div>
        <div class="adk_grid_list" style="margin-top:10px">

            <div class="ui_grid_header adk_columns">
                <div>Name</div>
                <div>Status</div>
                <div>Created Date</div>
                <div>Email</div>
