<script>

import uiListMixinLib                   from './../../mixins/lib/ui-list.js';
import {bus}                            from './../../config_admin_super.js';

export default {
    mixins : [uiListMixinLib],
    props: [],
    data: function() {
        return {
            appName: this.$route.params.app_name,
            tenantId : this.$route.params.tenant_id,
            tenantName : this.$route.params.tenant_name,
            api : "users",
        };
    },
    methods: {

        goToHomePage: function() {
            this.$router.push({
                name: 'home-page',
                params: {}
            });
        },
        goBack : function(){
          this.$router.push({
              name: 'tenant-list',
              params: {}
          });
        },
        createNewUser: function() {

            this.$router.push({
                name: 'new-user-reg',
                params: {}
            });
        },
        createNewUser: function() {

            this.$router.push({
                name: 'tenant-user-form-route',
                params: {}
            });
        },
        getUserList: function() {

            // If all records are fetched, make no calls to the server again.
            if(this.allRecordsFetched)
              return false;

            let _this = this;

            //this.list = [];

            let searchQuery = (this.searchQuery!=="")?this.searchQuery:"all";

            let encodedUserInfo = this.getCookie("userInfo");
            let decodedUserInfoString = decodeURIComponent(encodedUserInfo);
            let userInfo = JSON.parse(decodedUserInfoString);
            let userID = userInfo["uuid"];


            let get_url = "./users/list/" + this.pageNumber + "/" + this.tenantId + "/" + searchQuery; // + userID;
            //console.log("Retrieving data from : " + get_url);

            // VueJS ajax call-1
            axios.get(get_url)
                .then((dataResponse) => {

                    //console.log("user Registration Result : ");
                    //console.log(dataResponse);

                    if (dataResponse.data["actionResult"] === 1) {

                        let listOfUsers = (dataResponse.data["bean"]);//.slice(0, 10);

                        // Since we are pulling 20 records per page, then if the resulting size is lesser than 20, then it means
                        // no more records are available.
                        if(listOfUsers.length <20)
                        {
                          this.allRecordsFetched = true;
                        }

                        listOfUsers.forEach(user => {

                            if(user.createdOn !== null)
                            {
                                user.createdOn = new Date(user.createdOn).toLocaleString()
                                                                         .slice(0, 17)
                                                                         .replace(',', '')
                                                                         .replace('T',' ');
                            }

                            if(user.isOnline!==null && user.isOnline===1)
                            {
                                user.isOnline = "LIVE";
                            }

                            this.list.push(user);
                        });

                    } else {
                        console.error("Error", dataResponse);
                        //alert("Error : " + dataResponse)
                    }


                });

        },


        updateFetchURL(){
            let searchQuery = (this.searchQuery!=="")? this.searchQuery: "all";
            this.url = "./" + this.api + "/list/" + this.pageNumber + "/" + this.tenantId + "/" + searchQuery;
        },
        handleData(dataResponse){

            if (dataResponse.data["actionResult"] === 1) {

                let listOfUsers = (dataResponse.data["bean"]);//.slice(0, 10);

                // Since we are pulling 20 records per page, then if the resulting size is lesser than 20, then it means
                // no more records are available.
                if(listOfUsers.length <20)
                {
                  this.allRecordsFetched = true;
                }

                listOfUsers.forEach(user => {

                    if(user.createdOn !== null)
                    {
                        user.createdOn = new Date(user.createdOn).toLocaleString()
                                                                 .slice(0, 17)
                                                                 .replace(',', '')
                                                                 .replace('T',' ');
                    }

                    if(user.isOnline!==null && user.isOnline===1)
                    {
                        user.isOnline = "LIVE";
                    }

                    this.list.push(user);
                });

            }
            else {
                console.error("Error", dataResponse);
                //alert("Error : " + dataResponse)
            }
        },

    },
    created: function(){
    },
    mounted: function(){
        this.updateFetchURL();
        this.getList(); // Call to the /mixins/lib/ui-list.js mixin-file which makes a callback to handleData() method in this component.
    },
    watch: {

        // https://router.vuejs.org/en/advanced/data-fetching.html for more options.
        /*
            // call again the method if the route changes
            '$route': 'fetchData' where fetchData is a method defined above.
        */

        '$route.params.app_name' (newAppName, oldAppName) {


            //console.log('AppName changed from ' + oldAppName + ' to ' + newAppName);
            this.appName = newAppName;
        }


    }
}

</script>
