      cursor: pointer;
      width: 100%;
  }

</style>


<template>

  <div style="background: whitesmoke;display:grid;grid-template-columns:1fr;width:100%;place-items:start center;overflow-y:overlay" class="custom-scroll-bar">


      <div class="stage" style="display: grid;grid-template-columns:1fr 1fr">
          <div class="" style="padding-top: 50px;width: 50%;place-self: start end;">
              <form class="uk-grid-small" uk-grid style="font-family:lato" onsubmit="return false;">

                  <div class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Tenant <span uk-icon="icon:triangle-right;ratio:0.65"></span> Users <span uk-icon="icon:triangle-right;ratio:0.65"></span> Modify User</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: album;ratio:0.75"></span>
                              <input id="firstName" type="text" class="uk-input" v-model="tenantName" style="font-family: Lato;font-weight: 300;background-color: rgb(247, 247, 247);user-select: none;pointer-events: none;" autofocus="true" readonly>
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-1">
                    <hr class="uk-divider-icon">
                  </div>

                  <div class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">First Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: user;ratio:0.75"></span>
                              <input id="firstName" type="text" class="uk-input"  placeholder="First Name"  v-model="userObject.firstName" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Last Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input id="name" type="text" class="uk-input"  placeholder="Last Name"  v-model="userObject.lastName" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Email</label>
                      <div class="uk-form-controls">
                        <div class="uk-inline uk-width-1-1">
                            <span class="uk-form-icon" uk-icon="icon: mail;ratio:0.75"></span>
                            <input id="email" type="text"  class="uk-input"  placeholder="Email"  v-model="userObject.email" style="font-family: Lato;font-weight: 300;" autofocus="true">
                        </div>
                      </div>
                  </div>
		  <div class="uk-width-1-2@s"></div>

                  <div v-if="userObject.id === undefined" class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Password</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: lock;ratio:0.75"></span>
                              <input id="name" type="password"  class="uk-input"  placeholder="Password"  v-model="userObject.password" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div v-if="userObject.id === undefined" class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Confirm Password</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: lock;ratio:0.75"></span>
                              <input id="name" type="password"  class="uk-input"  placeholder="Confirm Password"  v-model="password2" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div class="uk-margin">
                      <div class="uk-inline">
                          <button id="saveButton" class="clickable-btn uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(240, 80, 110); border-radius: 2px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="save()" >Update</button>
                          &nbsp;&nbsp;&nbsp;
                          <button class="clickable-btn uk-button uk-button-danger uk-button-small uk-first-column end-call-button" style="background-color: rgba(253, 253, 253, 0.99); color: rgb(111, 111, 111); border: 1px solid rgb(183, 183, 183); border-radius: 3px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="goBack()" >Exit</button>
                      </div>
                  </div>
                </form>

          </div>
          <div style="width: 50%;place-self: center start;padding-left: 50px;">

              <div class="uk-width-1-2@s" style="width: 160px;padding-bottom: 10px;">
                  <div class="uk-form-controls">
                      <div class="uk-inline uk-width-1-1">
                          <img id="uploadedImage"  @error="profileImageLoadError()"  style="object-fit: cover;background: white;border: 5px solid white;border-radius: 50%;height: 160px;width: 160px;" v-bind:src="'/profile/get/' + uploadedPicId"
                          onerror="this.onerror=null;this.src='resources/images/male_default_pic.png';this.no_pic='true'" />
                      </div>
                  </div>
              </div>
              <div v-if="hasProfileImage" class="uk-width-1-2@s">
                  <div class="uk-form-controls">
                      <div class="uk-inline uk-width-1-1">
                        <div class="file-upload-wrapper" data-text="Change Image">
                          <input name="file" type="file" id="fileUploadForm" v-on:change="uploadPic($event.target.name, $event.target.files);" class="file-upload-field" value="">
                        </div>
                      </div>
                  </div>
              </div>
              <div v-if="!hasProfileImage" class="uk-width-1-2@s">
                  <div class="uk-form-controls">
                      <div class="uk-inline uk-width-1-1">
                        <div class="file-upload-wrapper" data-text="Upload Image">
                          <input name="file" type="file" id="fileUploadForm" v-on:change="uploadPic($event.target.name, $event.target.files);" class="file-upload-field" value="">
                        </div>
                      </div>
                  </div>
              </div>
          </div>
        </div>
    </div>
</template>


<script>


import userMixinLib                   from './../../mixins/lib/user_mixin_lib';
import utilsMixinLib                  from './../../mixins/lib/utils_lib';
    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib
                    ],
        props: [],
        data: function () {
            return {
                appName: "/oct.io",
                tenantId : this.$route.params.tenant_id,
                tenantName : this.$route.params.tenant_name,
                userObject :{
                    id : this.$route.params.id,
                },
                userId : null,
                username : null,
                numberOfParticipants : 2,
                description : "",
                firstName:"",
                lastName:"",
                email:"",
                password:"",
                password2:"",
                hasProfileImage : true,
                uploadedPicId : ""
            };
        },
        methods: {
            profileImageLoadError(){
              this.hasProfileImage = false;
            },
            uploadPic(fieldName, fileList){


              let _this_component = this;
               // handle file changes
               const formData = new FormData();
                  formData.append("name", this.userObject["id"]);


               if (!fileList.length) return;

               // append the files to FormData
               Array.from(Array(fileList.length).keys())
                    .map(x => {
                       formData.append(fieldName, fileList[x], fileList[x].name);
                    });

               // Display the key/value pairs
               for(var pair of formData.entries()) {
                  //console.log(pair[0]+ ', '+ pair[1]);
               }

              var url =  '/profile/upload';
            	var xhr = new XMLHttpRequest();
            		xhr.open('POST', url);
            		  xhr.setRequestHeader("ENCTYPE","multipart/form-data");
            		  //xhr.setRequestHeader("Content-Type","multipart/form-data;boundary=CUSTOM");
            		  xhr.send(formData);


            		// Setup our listener to process completed requests
            			xhr.onload = function() {

            				// Process our return data
            				if (xhr.status >= 200 && xhr.status < 300) {
            					// This will run when the request is successful
            					console.log('success!', xhr);
                      let savedImageName = xhr["responseText"];
                      console.log("savedImageName=" + savedImageName);
                      _this_component.uploadedPicId = savedImageName;
                      $("#uploadedImage").attr("src","/profile/get/" + savedImageName);

            					self.postMessage("Processed data from Worker.");
            				} else {
            					// This will run when it's not
            					console.log('The request failed!');
            				}

            				// This will run either way
            				// All three of these are optional, depending on what you're trying to do
            				//console.log('This always runs...');
            			};

            },
            goBack: function(){
                this.$router.push({
                    name: 'tenant-users-list',
                    params: {
                      tenant_id : this.tenantId,
                      tenant_name: this.tenantName
                    }
                });
            },
            doValidation : function()
            {

                if(this.userObject.firstName.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Firstname.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.userObject.lastName.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Lastname.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.userObject.email.trim()==="") // || (this.email.trim()!=="" && this.email.indexOf("@")===-1))
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid email.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.userObject.id==='new' && this.userObject.password !== this.password2)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Passwords Mismatch.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }

                return true;
            },
            getRecord : function(){

                console.log("getRecord");
                let _this_component = this;

                if(_this_component.userObject.id === "new")
                {
                    _this_component.userObject = {
                        id:"new"
                    };
                    return false;
                }

                let get_url = "./users/get/" + _this_component.userObject.id;
                console.log(get_url);

                axios.get(get_url)
                        .then(function (dataResponse) {

                            console.log(dataResponse);
                            if(dataResponse.data["actionResult"]==1)
                            {
                                let userObject = dataResponse.data["bean"];
                                _this_component.userObject = userObject;
                                _this_component.uploadedPicId = userObject["id"];
                                return false;
                            }

                        });


            },
            save : function(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.


                let btnText = document.getElementById("saveButton").innerHTML;
                document.getElementById("saveButton").innerHTML = "Saving..";
                this.disableHTMLElement(document.getElementById("saveButton"));

                let _this_component = this;
                let post_url = "./../users/save";
                console.log("Posting data to : " + post_url);




                let form = {
                    id :      this.userObject.id,
                    firstName :   this.userObject.firstName,
                    lastName  :   this.userObject.lastName,
                    email     :   this.userObject.email,
                    tenantId     :   this.tenantId
                }

                if(this.userObject.id===undefined)
                {
                    form["id"] = 'new';
                    form["password"]  =   this.userObject.password;
                }


                let formString = JSON.stringify(form,null,null);


                // VueJS ajax call-1
                axios.post(post_url, form)
                          .then((dataResponse) => {

                            console.log("User Registration Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                _this_component.newUserInfo = dataResponse.data["bean"];

                                UIkit.notification("<span uk-icon='icon: check;ratio: 0.65'></span> User updated successfully",{
                                    status: 'success',
                                    pos: 'top-right',
                                    timeout: 5000
                                });

                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                            }
                            else
                            {
                                alert("Error : " + dataResponse)
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.body["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("saveButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },
            logInfo: function (msg){

                let consoleLog = this.consoleLogTxt + " \n " + msg;
                this.consoleLogTxt = consoleLog;

                if (this.displayConsoleLog)
                {
                    console.log(msg);
                }
            },
        },
        mounted: function () {
            this.getRecord();
        },
        created: function () {
            console.error("WELCOME TO NEW USER REGISTRATION FORM.");
        }
    }

</script>
